<script>
import { inject, ref, onMounted, onUnmounted } from 'vue';
import gsap from '@gsap/shockingly';
// import ScrollTrigger from "@gsap/shockingly/ScrollTrigger";
import Draggable from "@gsap/shockingly/Draggable";

// import Input from '@/graphics/Input';
// import Device from '@/pure/Device';

export default {
  setup() {
    let app = inject('graphics');
    const corps = ref(null);
    const hero = ref(null);


    const variables = {
      rotationY: 6.28 - 1.3125,
      velocity: 0.0
    };

    const proxy = document.createElement("div");
    let drag = null;
    let startProgress = 0;
    const progressWrap = gsap.utils.wrap(0, 1);
    const dragDistancePerRotation = 3000;
    const spin = gsap.fromTo(variables, {
      rotationY: 6.28
    }, {
      paused: true,
      rotationY: 0,
      duration: 20,
      ease: "none",
      repeat: -1,
    });

    // let st = null;
    // let velocityTween = null;
    // const updateVelocity = (velocity) => {
    //   if (velocityTween) {
    //     velocityTween.kill();
    //   }
    //   velocityTween = gsap.timeline({
    //     onUpdate: () => {
    //       Input.velocity = variables.velocity;
    //     }
    //   })
    //   const f = Math.min(1.0, Math.abs(velocity) / Device.viewport.height);
    //   const v = f * -Math.sign(velocity);

    //   velocityTween.set(variables, {
    //     velocity: v,
    //     duration: 0.05,
    //     ease: 'sine.in'
    //   }, 'a')
    //   velocityTween.to(variables, {
    //     velocity: 0,
    //     duration: 0.2 * f,
    //     ease: 'expo.out'
    //   }, 'a+=0.05')
    // }
    
    onMounted(() => {
      // app.output.addBackground({ el: corps.value })

      // st = new ScrollTrigger({
      //   trigger: document.body.querySelector("#app"),
      //   scrub: true,
      //   start: "top top-=1px",
      //   end: "bottom bottom+=1px",
      //   onUpdate: (self) => {
      //     const { end, progress } = self;
      //     app.output.updateY({
      //       end, progress
      //     });
      //     updateVelocity(self.getVelocity());
      //   }
      // });

      spin.progress(1.3125 / 6.28);
      const updateRotation = () => {
        let p = startProgress + (drag[0].startX - drag[0].x) / dragDistancePerRotation;
        spin.progress(progressWrap(p));
        if (app.output.components.atlas.sphere) {
          app.output.components.atlas.sphere.rotation.y = variables.rotationY;
          app.output.components.atlas.sphere.updateMatrix();
        }
      }
      drag = Draggable.create(proxy, {
        trigger: hero.value,
        type: "x",
        inertia: true,
        allowNativeTouchScrolling: true,
        onPress() {
          gsap.killTweensOf(spin);
          spin.timeScale(0);
          startProgress = spin.progress();
        },
        onDrag: updateRotation,
        onThrowUpdate: updateRotation,
      });
    });
    onUnmounted(() => {
      // app.output.disposeComponent('background');
      // st.kill();

      drag[0].kill();
    });

    return {
      corps,
      hero
    }
  }
}
</script>

