import {
  Vector2,
  // Vector3,
  Mesh,
  // Uniform,
  Color,
  RawShaderMaterial,
  // TextureLoader,
  PlaneGeometry,
  GLSL3
} from "three";
import vertexShader from "./vertex.glsl";
import fragmentShader from "./fragment.glsl";

// import Device from "@/pure/Device";

// import gsap from "@gsap/shockingly";

export default class Circle extends Mesh {
  constructor({
    scene,
    diameter = 160,
    circle = 0,
    opacity = 0.5,
    visibility = 1,
    scale = 0.0625,
    renderOrder = 2,
    color = 0xffffff,
    mobile = false
  }) {
    const material = new RawShaderMaterial({
      glslVersion: GLSL3,
      transparent: true,
      uniforms: {
        opacity: { type: "f", value: opacity },
        visibility: { type: "f", value: visibility },
        circle: { type: "f", value: circle },
        scale: { type: "f", value: scale },
        color: { type: "c", value: new Color(color) },
        resolution: { type: "v2", value: new Vector2(0, 0) },
        arc: { type: "v2", value: new Vector2(0.0, 0.0) }
      },
      vertexShader,
      fragmentShader,
      depthTest: renderOrder ? false : true
    });
    // console.log(this.geometry);
    const geometry = new PlaneGeometry(1, 1, 1);
    super(geometry, material);
    // this.material = material;
    this.scene = scene;
    this.diameter = diameter;
    this.opacity = opacity;
    this.mobile = mobile;
    this.matrixAutoUpdate = false;
    this.interpolate = {
      x: 0,
      y: 0,
      arc2: 0.0,
      circle
    };
  }
  render() {}
  resize() {
    this.scale.x = this.diameter;
    this.scale.y = this.diameter;
    this.updateMatrix();
  }
  dispose() {
    this.geometry.dispose();
    this.material.dispose();
  }
}